body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  width: 50%;
  min-width: 370px;
  margin: 0 auto;
}

.MessageList {
  height: 80vh;
  overflow: scroll;
}

.Enter {
  width: 100%;
}

.Enter textarea {
  width: 80%;
}

.msg-dtl div {
  min-width: 250px;
}

.Message {
  overflow: hidden;
}
.MyText {
  float: right;
  text-align: right;
}
.MyText .msg-dtl div {
  background-color: green !important;
}
.MyText .msg-dtl div span {
  color: #fff !important;
}
